import { CacheProvider } from './../../../providers/cache-provider';
import { ViewController } from 'ionic-angular';

import { Events } from 'ionic-angular';
import { NativeService } from './../../../services/native-service';
import { Component } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, ToastController, ModalController } from 'ionic-angular';

import { CountryProvider, AuthProvider, UserProvider } from '../../../providers/providers';
import { StorageService } from '../../../services/services';

import { MainPage } from '../../pages';

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html'
})
export class SignupPage {
  // The account fields for the login form.
  // If you're using the username field with or without email, make
  // sure to add it to the type
  account: { country: string, mobilephone: string, verifyCode: string } = {
    country: "+86",
    mobilephone: "",
    verifyCode: ""
  };

  // Our translated text strings
  private signupErrorString: string;
  private sendCodeErrorString: string;
  private verifyButtonFixedText: string;
  private verifyButtonText: string;
  private verifyButtonEnabled: boolean = false;
  private switch: boolean = false;
  isReadPolicy: boolean = false; //是否已读隐私政策
  language: string; 
  COUNTDOWN_KEY: string = 'signUpCountDown'; //倒计时KEY
  constructor(public navCtrl: NavController,
    public modalCtrl: ModalController,
    public authProvider: AuthProvider,
    public userProvider: UserProvider,
    public toastCtrl: ToastController,
    public translateService: TranslateService,
    private storageService: StorageService,
    public countryProvider: CountryProvider,
    public nativeService: NativeService,
    public events: Events,
    public viewCtrl: ViewController,
    public cacheProvider: CacheProvider
  ) {

    this.validateInput();
    this.events.subscribe('countDown:send-security-code:' + this.COUNTDOWN_KEY,()=>{
      this._resetTime(60);

    })
    //是否存在上次倒计时，若是，则继续上一次倒计时。
    if (this.cacheProvider.getCountDown(this.COUNTDOWN_KEY) > 0) {
      this._resetTime(this.cacheProvider.getCountDown(this.COUNTDOWN_KEY));
    }

    this.nativeService.getSimInfo().subscribe((info) => {
      if (info.countryCode) {
        this.account.country = '+' + this.countryProvider.query(info.countryCode)[0].number;
      }
    }, (err) => {
    })
    // this.storageService.getItem("country").then((country)=>{
    //   this.account.country = country;
    // });
    this.translateService.get('SIGNUP_ERROR').subscribe((value) => {
      this.signupErrorString = value;
    })
    this.translateService.get('SIGNUP_VERIFYCODE_SEND').subscribe((value) => {
      this.verifyButtonFixedText = value;
      this.verifyButtonText = this.verifyButtonFixedText;
    })
  }

  ionViewDidLoad() {
    this.storageService.getItem("language").then((language) => {
      this.language = language;
    })
  }
  sendSecurityCodeBefore(){
    this.verifyButtonEnabled = true;   
    this.authProvider.sendSecurityCodeBefore('sendSecurityCodeBefore',this.account,this.COUNTDOWN_KEY,()=>{
      this.verifyButtonEnabled = false;
    }); 
  }
 
  doSignup() {
    //校验验证码
    this.authProvider.signup(this.account).subscribe((resp) => {
      this.storageService.setItem("mobilephone", this.account.mobilephone);
      this.storageService.setItem("country", this.account.country);

      if (resp.errcode == 0) {
        this.events.publish('closemodal:login-success')
        setTimeout(()=>{
          this.events.publish('register:success');
        },300)
        this.viewCtrl.dismiss();
      }
    }, (err) => {
      // Unable to sign up
      if (err.errcode == 6110) {
        this.signupErrorString = this.translateService.instant('SIGNUP_ERROR_NUMBER_EXIST');
      } else if (err.errcode == 6126) {
        this.signupErrorString = this.translateService.instant('SIGNUP_ERROR_INVALID_CODE');
      }
      let toast = this.toastCtrl.create({
        message: this.signupErrorString,
        duration: 3000,
        position: 'top'
      });
      toast.present();
    });
  }

  doSelectCountry() {
    let selectModal = this.modalCtrl.create('SelectCountryPage');
    selectModal.onDidDismiss(item => {
      if (item) {
        this.account.country = "+" + item.number;
      }
    })
    selectModal.present();
  }

  /**
   * 获取验证码
   */
  getVerifyCode() { 
    this.authProvider.sendSecurityCode(this.account,this.COUNTDOWN_KEY).subscribe((resp) => {
      this._resetTime(60); 
    });
  }

  _resetTime(time: number) {
    let that = this;
    that.verifyButtonEnabled = true;
    (function countdown() {

      // var s = time % 60
      // var m = Math.floor((time / 60)) % 60
      // var msg = `${(m < 10 ? '0' : '') + m}分钟${(s < 10 ? '0' : '') + s}`;
      that.verifyButtonText = time.toString() + " " + that.translateService.instant('APP_DATETIME_SECOND');
      if (--time > 0) {
        that.cacheProvider.setCountDown(that.COUNTDOWN_KEY, time);
        setTimeout(countdown, 1000)

      } else {
        // 做结束的事
        that.verifyButtonText = that.verifyButtonFixedText;
        that.verifyButtonEnabled = false;
      }
    })();
  }

  //判断提现页面的提现按钮是否可点击  
  public cashJ = { //定义一个空对象  
    cashInputValue: ''
  };


  /**
   * 判断按钮是否可用；
   * 1.手机号码为空时，所有按钮不可用。
   * 2.当手机号码不為空时，发送验证码按钮可用；验证码为6位时且同意协议时，下一步按钮可用。
   */
  validateInput() {
    if (this.account.mobilephone) {
      if(this.cacheProvider.getCountDown(this.COUNTDOWN_KEY)==0){
        this.verifyButtonEnabled = false;
      }
      if (this.account.verifyCode.length == 6) {
        this.switch = true;
      } else {
        this.switch = false;
      }
    } else {
      this.switch = false;
      this.verifyButtonEnabled = true;
    }
  }
  exchangeState() {
    this.isReadPolicy = !this.isReadPolicy;
  }

  goBack(){
    this.viewCtrl.dismiss();
  }
 

}



